html {
  box-sizing: border-box;
  font-size: 10px;
}

*,
::before,
::after {
  box-sizing: inherit;
  padding: 0px;
  margin: 0px;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
  background-color: transparent;
}
